.x1z2Chat-chatbot-toggler, .x1z2Chat-chatbot-header, .x1z2Chat-chatbox .x1z2Chat-chat p {
  background: var(--primary-color);
}

.x1z2Chat-chat-input span {
  color: var(--primary-color);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.x1z2Chat-chatbot-toggler {
  cursor: pointer;
  z-index: 998;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  transition: all .2s;
  display: flex;
  position: fixed;
  bottom: 30px;
  right: 35px;
  box-shadow: 0 1px 4px #0000001a, 0 2px 12px #0003;
}

.x1z2Chat-show-chatbot .x1z2Chat-chatbot-toggler {
  transform: rotate(90deg);
}

.x1z2Chat-chatbot-toggler span {
  color: #fff;
  font-size: 34px;
  position: absolute;
}

.x1z2Chat-chatbot-toggler span:last-child, .x1z2Chat-show-chatbot .x1z2Chat-chatbot-toggler span:first-child {
  opacity: 0;
}

.x1z2Chat-show-chatbot .x1z2Chat-chatbot-toggler span:last-child {
  opacity: 1;
}

.x1z2Chat-chatbot {
  opacity: 0;
  pointer-events: none;
  transform-origin: 100% 100%;
  z-index: 1000;
  background: #fff;
  border-radius: 15px;
  width: 420px;
  font-family: Inter, sans-serif;
  transition: all .25s ease-in-out;
  position: fixed;
  bottom: 113px;
  right: 46px;
  overflow: hidden;
  transform: scale(.5);
  box-shadow: 0 0 128px #0000001a, 0 32px 64px -48px #00000080;
}

.x1z2Chat-chatbot-header {
  color: #fff;
  justify-content: flex-start;
  align-items: center;
  padding: 16px;
  display: flex;
  position: relative;
}

.x1z2Chat-chatbot-logo {
  object-fit: cover;
  border: 3px solid #fff;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-right: 15px;
}

.x1z2Chat-chatbot-header-text h2 {
  color: #fff;
  margin-bottom: 4px;
  font-family: Inter, sans-serif;
  font-size: 1rem;
  font-weight: 600;
}

.x1z2Chat-online-status:before {
  content: "" !important;
  vertical-align: middle !important;
  background-color: #4ece3d !important;
  border-radius: 50% !important;
  flex: none !important;
  width: 8px !important;
  height: 8px !important;
  margin-right: 5px !important;
  display: inline-block !important;
  position: relative !important;
  top: -1.6px !important;
}

.x1z2Chat-chatbot-header-text p {
  font-size: .92rem;
  font-weight: 300;
}

.x1z2Chat-show-chatbot .x1z2Chat-chatbot {
  opacity: 1;
  pointer-events: auto;
  transform: scale(1);
}

.x1z2Chat-chatbot header span {
  cursor: pointer;
  display: none;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}

header h2 {
  font-size: 1.4rem;
}

.x1z2Chat-chatbot .x1z2Chat-chatbox {
  height: 510px;
  padding: 30px 20px 100px;
  overflow-y: auto;
}

.x1z2Chat-chatbot :where(.x1z2Chat-chatbox, textarea)::-webkit-scrollbar {
  width: 6px;
}

.x1z2Chat-chatbot :where(.x1z2Chat-chatbox, textarea)::-webkit-scrollbar-track {
  background: #fff;
  border-radius: 25px;
}

.x1z2Chat-chatbot :where(.x1z2Chat-chatbox, textarea)::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 25px;
}

.x1z2Chat-chatbox .x1z2Chat-chat {
  list-style: none;
  display: flex;
}

.x1z2Chat-chatbox .outgoing {
  justify-content: flex-end;
  margin: 20px 0;
}

.x1z2Chat-chatbox .incoming .x1z2Chat-assistant-avatar {
  object-fit: cover;
  border-radius: 50%;
  align-self: flex-end;
  width: 45px;
  height: 45px;
  margin: 0 10px -25px 0;
}

.x1z2Chat-chatbox .x1z2Chat-chat p {
  white-space: pre-wrap;
  color: #fff;
  border-radius: 10px 10px 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 50px;
  max-width: 78%;
  padding: 12px 17px;
  font-size: 1rem;
  display: flex;
}

.x1z2Chat-chatbox .x1z2Chat-chat p a {
  color: #007bff;
  text-decoration: none;
}

.x1z2Chat-chatbox .x1z2Chat-chat p a:hover, .x1z2Chat-chatbox .x1z2Chat-chat p a:focus {
  color: #0056b3;
  text-decoration: underline;
}

.x1z2Chat-chatbox .x1z2Chat-chat p img {
  cursor: pointer;
  border-radius: 5px;
  max-width: 100%;
  height: auto;
  margin-top: 15px;
  display: block;
}

.x1z2Chat-chatbox .incoming p {
  border-radius: 10px 10px 10px 0;
}

.x1z2Chat-chatbox .x1z2Chat-chat p.error {
  color: #721c24;
  background: #f8d7da;
}

.x1z2Chat-chatbox .incoming p {
  color: #424b53;
  background: #eaf0f6;
  font-weight: 440;
}

.x1z2Chat-chatbox .incoming p strong {
  padding-top: 15px;
  display: inline-block;
}

.x1z2Chat-chatbot .x1z2Chat-chat-input {
  background: #fff;
  border-top: 1px solid #ddd;
  gap: 5px;
  width: 100%;
  padding: 3px 20px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.x1z2Chat-chat-input textarea {
  resize: none;
  border: none;
  outline: none;
  width: 100%;
  height: 55px;
  max-height: 180px;
  padding: 15px 15px 15px 0;
  font-family: Inter, sans-serif;
  font-size: 1rem;
}

.x1z2Chat-chat-input span {
  cursor: pointer;
  visibility: hidden;
  align-self: flex-end;
  align-items: center;
  height: 55px;
  font-size: 1.8rem;
  display: flex;
}

.x1z2Chat-chat-input textarea:valid ~ span {
  visibility: visible;
}

@media (width <= 490px) {
  .x1z2Chat-chatbot-toggler {
    bottom: 20px;
    right: 20px;
  }

  .x1z2Chat-chatbot {
    border-radius: 0;
    width: 100%;
    height: 100%;
    bottom: 0;
    right: 0;
  }

  .x1z2Chat-chatbot .x1z2Chat-chatbox {
    height: calc(100vh - 140px);
    padding: 25px 15px 100px;
    overflow-y: auto;
  }

  .x1z2Chat-chatbot .x1z2Chat-chat-input {
    padding: 5px 15px;
  }

  .x1z2Chat-chatbot header span {
    display: block;
  }
}

.x1z2Chat-lightbox-modal {
  z-index: 1000;
  background-color: #000000e6;
  width: 100%;
  height: 100%;
  padding-top: 100px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.x1z2Chat-lightbox-modal img {
  width: 80%;
  max-width: 700px;
  margin: auto;
  display: block;
}

.x1z2Chat-close-lightbox {
  color: #f1f1f1;
  cursor: pointer;
  font-size: 40px;
  font-weight: bold;
  position: absolute;
  top: 25px;
  right: 35px;
}

.x1z2Chat-typing {
  position: relative;
}

.x1z2Chat-typing span {
  background: #3b5998;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  animation: 1.5s infinite both blink;
  position: absolute;
  top: 0;
  left: 0;
}

.x1z2Chat-typing span:nth-child(2) {
  margin-left: 15px;
  animation-delay: .2s;
}

.x1z2Chat-typing span:nth-child(3) {
  margin-left: 30px;
  animation-delay: .4s;
}

@keyframes blink {
  0% {
    opacity: .1;
  }

  20% {
    opacity: 1;
  }

  100% {
    opacity: .1;
  }
}

.x1z2Chat-chat-popup {
  color: #6f6f6f;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  background: #fff;
  border-radius: 8px;
  width: auto;
  max-width: 250px;
  padding: 40px 20px 20px;
  font-family: Urbanist, sans-serif;
  font-weight: 400;
  line-height: 1.6;
  transition: opacity .5s, visibility .5s;
  position: fixed;
  bottom: 110px;
  right: 35px;
  box-shadow: 0 4px 8px #0000001a;
}

.x1z2Chat-chat-popup .x1z2Chat-close-popup {
  cursor: pointer;
  z-index: 3;
  padding: 10px;
  font-size: 25px;
  position: absolute;
  top: -13px;
  right: 3px;
}

.x1z2Chat-popup-assistant-avatar {
  object-fit: cover;
  z-index: 2;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  position: absolute;
  top: -22.5px;
  left: 50%;
  transform: translateX(-50%);
}

@media (width <= 600px) {
  .x1z2Chat-popup-assistant-avatar {
    width: 35px;
    height: 35px;
    top: -17.5px;
  }

  .x1z2Chat-chat-popup {
    padding-top: 30px;
    bottom: 103px;
    right: 20px;
  }
}

